<template>
  <div class="text">
    <!-- 导航 -->
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <!-- 内容 -->
    <div class="content" v-if="content">
      <div class="content-box">
        <div class="content-title">
          <h2>{{ content.title }}</h2>
          <a href="#/">&lt;&lt;{{ $t('text.btn') }}</a>
        </div>
        <div class="content-line"></div>
        <div class="content-list">
          <div class="content-detail">

          </div>
          <div class="content-footer">
            <p v-if="content.remark1">{{ content.remark1 }}</p>
            <p v-if="content.remark2">{{ content.remark2 }}</p>
            <p v-if="content.remark3">{{ content.remark3 }}</p>
            <p>{{ content.time }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import { getText } from "@/api/index"

export default {
  components: {
    AoNav
  },
  data() {
    return {
      content: {

      }
    };
  },
  mounted() {
    getText({ id: this.$route.query.id }).then(res => {
      this.content = res
      if (res && res.content) {
        document.querySelector('.content-detail').innerHTML = res.content || ''   
      }
    })
  }
};
</script>

<style lang="scss">
.text {
  .content {
    background: url(/static/image/account_center_bg.jpg) no-repeat;
    background-size: cover;
  }
  .content-box {
    width: 1200px;
    margin: 0 auto;
    padding-top: 30px;
    // overflow: hidden;
    min-height: 880px;
  }
  .content-title {
    height: 72px;
    line-height: 72px;
    padding: 0 30px;
    border-radius: 3px;
    background-color: #1a243b;
    h2,
    a {
      color: #637085;
      font-size: 20px;
      float: left;
      cursor: pointer;
    }
    a {
      float: right;
    }
  }
  .content-line {
    height: 10px;
  }
  .content-list {
    width: 100%;
    min-height: 750px;
    background-color: #1a243b;
    padding: 48px 46px 20px;
  }
  .content-detail,.content-footer{
    color: #637085;
    line-height: 26px;
  }
  .content-footer{
    margin-top: 40px;
  }
  .content-footer  p{
    text-align: right;
  }
}
</style>



